header
    position: relative
    height: 50px

nav
    position: fixed
    width: 100%
    height: 50px
    background-color: #212121
    z-index: 5
.hamburger
    display: none

.menu
    display: flex
    justify-content: space-between
    padding: 13px 70px
    margin: 0
    list-style-type: none
    &_link 
        height: 31px 
        font-weight: 300 
        font-size: 15px 
        color: #FFFFFF 
    &_item 
        border-right: 2px dotted wheat 
        padding-right: 50px
        &:last-child
            border-right: none
.subheader 
    position: absolute 
    width: 100% 
    height: 84px 
    background-color: rgba(0,0,0, .6) 
    opacity: 0.6
    padding-top: 60px
    .subheader_logo 
        display: block 
        margin-top: 16px 
        max-width: 100% 
    .subheader_official 
        margin-top: 6px 
        opacity: 0.5 
        width: 255px 
        height: 68px 
        font-weight: 100 
        font-size: 13px 
        color: #FFFFFF 
    .subheader_call 
        margin-top: 16px 
        width: 166px 
        height: 14px 
        font-weight: 300 
        font-size: 12px 
        color: #FFFFFF 
    .subheader_phone 
        display: block 
        margin-top: 8px 
        color: #ffffff 
        font-size: 20px 
        font-weight: 400 
        text-transform: uppercase 
        text-align: right 
        line-height: 12px 
    .subheader_btn 
        margin-top: 18px 
        width: 172px 
        height: 46px 
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%) 
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12) 
        border-radius: 4px 
        border: none 
        width: 162px 
        height: 46px 
        font-weight: 400 
        font-size: 14px 
        line-height: 46px 
        text-align: center 
        text-transform: uppercase 
        color: #FFFFFF 
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21)
        +btn_hover
.promo 
    min-height: 800px 
    background: url("../img/uber_bg.png") center (center / cover) no-repeat 
    padding: 144px 0 143px 0
    .promo_header 
        font-weight: 700 
        font-size: 60px 
        text-transform: uppercase 
        color: #FFFFFF 
        line-height: 60px 
        margin: 0 
        text-align: center 
    .promo_subheadeer 
        font-weight: 700 
        font-size: 36px 
        text-transform: uppercase 
        color: #FFFFFF 
        line-height: 36px 
        margin: 0 
        margin-top: 22px 
        text-align: center 
    .promo_descr 
        font-weight: 400 
        font-size: 18px 
        line-height: 24px 
        text-align: center 
        color: #FFFFFF 
        margin-top: 18px 
    .promo_btn 
        display: block 
        margin: 180px auto 0 auto 
        margin-top: 150px 
        width: 172px 
        height: 46px 
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%) 
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12) 
        border-radius: 4px 
        border: none 
        width: 162px 
        height: 46px 
        font-weight: 400 
        font-size: 14px 
        line-height: 46px 
        text-align: center 
        text-transform: uppercase 
        color: #FFFFFF 
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21) 
        +btn_hover
.reasons 
    padding: 71px 0 65px 0 
    .reasons_block 
        display: flex 
        justify-content: center 
        align-items: flex-start
        margin-top: 35px 
        .reasons_round 
            display: flex 
            justify-content: center 
            align-items: center 
            margin-right: 26px 
            width: 113px 
            height: 113px 
            background-color: #1eacc7 
            border-radius: 100% 
        .reasons_descr 
            width: 343px 
            .reasons_subtitle 
                line-height: 21px 
                color: #1eacc7 
                font-size: 17px 
                font-weight: 400 
            .reasons_text 
                color: #202020 
                font-size: 15px 
                font-weight: 300 
                line-height: 21px 
.label 
    margin: 0 auto 
    height: 24px 
    width: 112px 
    padding: 5px 12px 
    background-color: #000000 
    color: #ffffff 
    font-size: 11px 
    font-weight: 300 
    text-transform: uppercase 
.title 
    margin-bottom: 0 
    margin-top: 38px 
    color: #222222 
    font-size: 38px 
    font-weight: 700 
    text-transform: uppercase 
    text-align: center
.subtitle
    font-weight: 400px
    font-size: 17px
    color: #5b0c0c
    margin-top: 19px
    text-align: center
.mobile
    min-height: 864px
    padding: 40px 0 79px 0
    background: url("..img/Uber(1).png") center (center / cover) no repeat
    .row
        margin-top: 22px
    &_item
        position: relative
        width: 320px
        height: 280px
        margin: 0 auto
        margin-top: 24px
        padding: 92px
        &_1
        background: URL("../img/card1.png")
        &_2
        background: URL("../img/card2.png")
        &_3
        background: URL("../img/Карточка 3.png")
        &_4
        background: URL("../img/Карточка 4.png")
        &_5
        background: URL("../img/Карточка 5.png")
        &_6
        background: URL("../img/Карточка 6.png")
        &_subtitle
        font-weight: 400
        font-size: 20px
        text-align: center
        line-height: 31px
        text-transform: uppercase
        color: #FFFFFF
        &_plus
            display: flex
            justify-content: center
            align-items: center
            width: 26px
            height: 26px
            margin: 0 auto
            border-radius: 100%
            position: absolute
            bottom: 35px
            left: 50%
            transform: translateX(-50%)
            cursor: pointer
            &:before
                content: ''
                display: block
                width: 14px 
                height: 2px 
                background-color: $main-color
                transition: 0.3s all
            &:after 
                content: ''
                position: absolute 
                left: 50%
                transform: translateX(-50%)
                display: block 
                height: 14px
                width: 2px
                background-color: $main-color
                transition: 0.3s all
            &:hover 
                &:before, &:after 
                    background-color: $second-color
.choice
    position: relative
    min-height: 899px
    padding: 57px 0 138px 0
    &_titles
    margin-bottom: 0 
    margin-top: 38px 
    color: #f3eded 
    font-size: 38px 
    font-weight: 700 
    text-transform: uppercase 
    text-align: center
    &_car 
        position: relative
        display: block
        margin: auto 0
        margin-top: 80px
        z-index: 2
    .descr 
        text-align: center
        color: #ece0e0
        font-size: 17px
        font-weight: 300
        line-height: 24px
    .ddescr
        text-align: center
        color: #ece0e0
        font-size: 12px
        font-weight: 200
    .dark 
        position: absolute 
        top: 0
        left: 0
        height: 499px
        width: 100%
        background-color: #070716
        z-index: -1
.require
    padding-block: 68px 0 84px 0
    &_block
        margin: 33px 0
        &_mb
            margin-bottom: 0px
    &_title
        color: #e78878
        font-weight: 400
        font-size: 22px
    &_list
        list-style-type: none
        padding-left: 50px
        li
            position: relative
            font-size: 17px
            font-weight: 300
            line-height: 24px
            margin-bottom: 10px
            &::before
                content: ''
                display: block
                position: absolute
                left: -32px
                top: 3px
                width: 21px
                height: 20px
                background: url('../icons/Плюс.png') center (center / cover)
    &_descr
        margin-top: 25px
        font-size: 17px
        font-weight: 300
        line-height: 24px
        color: #11daf1
    .warning
        .require_title
        color: #c1178388
        .require_list
            li
                &::before
                    background: url('../icons/Эллипс.png') center (center / cover)
.world
    position: relative
    min-height: 717px
    padding: 49px 0
    background: url('../img/world_bg.png') center (center / cover)
    margin-top: 60px
    &_link 
            position: absolute
            left: 50%
            transform: translateX(-50%)
            bottom: 49px
            text-align: center
            font-size: 15px
            font-weight: 400
            line-height: 68px
            text-transform: uppercase
            color: #1fbad6
            &:hover 
                text-decoration: none
                color: #fff
    .title        
        margin-bottom: 0 
        margin-top: 38px 
        color: #f3eded 
        font-size: 38px 
        font-weight: 700 
        text-transform: uppercase 
        text-align: center
    .subtitle
        font-size: 14px
        color: #FFFFFF    
    .label
            width: 75px
            height: 22px
            font-size: 11px
            align-items: center
            text-align: center
            text-transform: uppercase
            color: #fff
            &_label_min
            color: #e778e7
footer 
    padding: 14px 23px 39px 23px
    a 
.footer
    &:hover 
        text-decoration: none
    &_divider
        width: 299px
        height: 4px
        background-color: #1fbad6
        margin: 0 auto
    &_wrapper
        display: flex 
        justify-content: space-between
        align-items: flex-start
        padding-top: 32px
    &_wrapper > div 
        width: 33%
        &:nth-child(3)
            display: flex 
            justify-content: flex-end
    &_social
        width: 160px
        display: flex
        justify-content: space-between
        &_item 
            display: flex 
            justify-content: center
            align-items: center
            margin: 0 4px 
            width: 32px
            height: 32px
            background-color: #070716
            border-radius: 100%
            i 
                color: #fff
    &_links
        width: 390px
        margin: 0 auto
        &_main 
            display: flex 
            justify-content: space-between
            padding: 0 10px
            a 
                color: #070716
                font-size: 15px
                font-weight: 300
                text-transform: uppercase
        &_sub 
            display: flex 
            justify-content: space-between
            margin-top: 10px
            a 
                color: #999999
                font-size: 11px
                font-weight: 300
                text-transform: uppercase
        &_lang 
            display: block 
            margin: 0 auto
            color: #1fbad6
            font-size: 11px
            font-weight: 400
            margin-top: 19px
            text-transform: uppercase
            text-align: center
    .xs-visible 
        display: none